import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';
import * as json2csv from 'json2csv'; // convert json file to csv
import { saveAs } from 'file-saver';  // save the file

@Injectable({
  providedIn: 'root'
})
export class PlantDataService {

  constructor(private http: Http) { }

  nodeUrl: string = "http://13.232.239.52:8999"

  getData() {

    return new Observable<any>((observer) => {

      return this.http.get(this.nodeUrl + '/getData')
        .pipe(map(response => <any>response.json()))
        .subscribe(data => {

          observer.next(data);

        },
          error => {
            // console.log(error)
          });
    });
  }

  postData(body) {

    return new Observable<any>((observer) => {

      return this.http.post(this.nodeUrl + '/postData', (body))
        .pipe(map(response => <any>response.json()))
        .subscribe(data => {

          observer.next(data);

        },
          error => {
            // console.log(error)
          });
    });
  }

  postArrayofProducts(body) {

    return new Observable<any>((observer) => {

      return this.http.post(this.nodeUrl + '/postArrayofProducts', (body))
        .pipe(map(response => <any>response.json()))
        .subscribe(data => {

          observer.next(data);

        },
          error => {
            // console.log(error)
          });
    });
  }

}
